.xphome2_main {
  box-sizing: border-box;
  background-color: #474e5d;
  font-family: Helvetica, sans-serif;
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.xphome2_main::after {
  content: "";
  position: absolute;
  width: 2px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -0.15%;
}

.xphome2_container {
  padding: 10% 5%;
  position: relative;
  background-color: inherit;
  width: 40%;
}

.xphome2_container::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  right: -0;
  background-color: white;
  border: 4px solid #ff9f55;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

.left {
  left: 0;
}

.right {
  left: 55%;
}

.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent red transparent transparent;
}

.right::after {
  left: -5%;
}

.xphome2_container_content {
  padding: 20px 30px;
  background-color: white;
  position: relative;
  border-radius: 6px;
}

.xphome2_container_bullet {
  background-color: white;
  position: absolute;
  border-color: rgb(19, 89, 151);
  width: 20px;
  height: 20px;
  left: calc(50% - 10px - 1px);
  z-index: 10;
  /*border: medium solid white;*/
  border-radius: 50%;
  border-style: solid;

  /*border: 3px solid #ff9f55;*/
}

@media screen and (max-width: 600px) {
  /* Place the timelime to the left */
  .xphome2_main::after {
    left: 31px;
  }

  /* Full-width containers */
  .xphome2_main_container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  /* Make sure that all arrows are pointing leftwards */
  .xphome2_main_container::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left::after,
  .right::after {
    left: 15px;
  }

  /* Make all right containers behave like the left ones */
  .right {
    left: 0%;
  }
}
