.content_xp {
  display: inline-block;
  padding-top: 10%;
  padding-bottom: 20%;
  margin-top: 20%;
  margin-bottom: 20%;
  background-color: rgb(255, 255, 255);
}

.content_xp_li_inicio {
  position: relative;
  list-style-type: none;
  margin-left: 30%;
  max-width: 40%;
  min-height: 10px;
  height: 50px;
}

.content_xp_timeline_inicio {
  left: -5%;
  background-color: rgb(163, 163, 163);
  position: absolute;
  width: 2px;
  min-height: 150px;
  height: 150px;
  text-align: center;
}

.content_xp_li_ultimo {
  position: relative;
  list-style-type: none;
  margin-left: 30%;
  max-width: 40%;
  min-height: 10px;
  height: 50px;
}

.content_xp_timeline_ultimo {
  left: -5%;
  background-color: white;
  position: absolute;
  width: 2px;
  min-height: 100px;
  height: 10px;
  text-align: center;
  top: -0px;
}

.content_xp .content_xp_li_ultimo span {
  width: 100px;
  position: absolute;
  left: -50px;
  padding: 5px 5px 5px 5px;
  background-color: rgb(163, 163, 163);
  color: white;
  font-size: small;
  letter-spacing: 3px;
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

.content_xp .content_xp_li_inicio .content_xp_timeline_dot_inicio {
  border-radius: 50%;
  border-style: solid;
  border-color: rgb(163, 163, 163);
  border-width: 2px;
  background-color: rgb(163, 163, 163);
  height: 2px;
  width: 2px;
  position: absolute;
  left: -7px;
  top: 0px;
  padding: 5px 5px 5px 5px;
  font-size: small;
  letter-spacing: 3px;
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

.content_xp_li_inicio .content_xp_element_container .content_xp_element_par {
  background-color: rgb(37, 116, 185);
  border-radius: 4px;
  border-color: black; /*sacar*/
  border-style: thin; /*sacar*/
  padding: 10px;
  margin-left: 20%;
  margin-bottom: 30px;
  width: 80%;
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

/* Elemento par - ajustado a la derecha */

.content_xp_li_par {
  position: relative;
  list-style-type: none;
  margin-left: 30%;
  max-width: 40%;
  min-height: 10px;
}

.content_xp_timeline_par {
  top: +80px;
  left: -5%;
  background-color: rgb(163, 163, 163);
  position: absolute;
  width: 2px;
  min-height: 200px;
  height: 100%;
  text-align: center;
  padding-bottom: -50px;
  margin-bottom: -50px;
}
.content_xp_element_container {
  padding-bottom: 20px;
}

.content_xp_element_par {
  position: relative;
  background-color: rgb(37, 116, 185);
  border-radius: 4px;
  border-color: black; /*sacar*/
  border-style: thin; /*sacar*/
  padding: 10px;
  margin-left: 20%;
  margin-bottom: 30px;
  width: 80%;
  height: 180px;
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

/*  Comunes */

.content_xp_li_par .content_xp_element_bar {
  position: relative;
  background-color: rgb(37, 116, 185);
  top: 25px;
  left: -15px;
  right: -2px;
  height: 3px;
}

.content_xp_timeline_dot {
  background-color: #006600;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border-style: solid;
  border-color: white;
  /*left: -11.75px;*/
  left: -6.5px;
  top: 20px;
  width: 10px;
  height: 10px;
}

.content_xp .content_xp_li_par span {
  width: 100px;
  position: absolute;
  left: -50px;
  /*top: +80px;*/
  padding: 5px 5px 5px 5px;
  background-color: #006600;
  color: white;
  font-size: small;
  letter-spacing: 3px;
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

.content_xp_li_par:hover .content_xp_element_container .content_xp_element_par {
  position: relative;
  transform: translateY(-5px);
  box-shadow: 0px 10px 10px rgb(163, 163, 163);
}
