.main_name {
  height: 70vh;
  border: 0px solid white;
  /*position: relative;*/
  /*transform: translate(-50%, -50%);*/
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  font-style: normal;
  letter-spacing: 8px;
  color: white;
  text-shadow: 1px 1px 2px black;
  overflow: hidden;
  position: relative;
  background-image: url("../../img/100_5970.JPG");
  /*background-image: url("../../img/20180103_174802_2.png");*/
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  background-position: top center;
  padding-top: 25vh;
  padding-bottom: 10vh;
}
.main_name .main_name_nombre {
  text-align: right;
  transform: translate(-45%, 0%);
}

.main_name .main_name_apellido {
  text-align: left;
  transform: translate(+45%, 0%);
}
