.d-flex .flex-row {
  padding: 0;
  margin: 0;
}

.content {
  /*width: 100%;*/
  /*min-height: 100vh;*/
  clear: both;
  float: left;

  padding: 0;
  margin: 0;
}

/****************************************************************************************************************/
.content_td_menubar {
  position: fixed;
  width: 20%;
  height: 100%;
}
.content_menubar {
  background-color: black;
  padding-top: 50%;
  padding-left: 20%;
  height: 100vh;
}
.content_menubar .flex-column {
  background-color: black;
}
.content_menubar .nav-link .h6 {
  background-color: black;
  color: white;
  letter-spacing: 3px;
  border-radius: 0%;
  text-align: left;
}
.content_menubar .nav-link.active {
  background-color: black;
  color: white;
  border-radius: 0%;
}

.content_menubar .nav-link.active .h4:hover {
  color: white;
}

.content_menubar .flex-column.nav.nav-pills {
  border-radius: 0px;
}

/****************************************************************************************************************/

.content_td_active {
  background-color: white;
  padding: 0%;
  height: 100%;
  height: 70vh;
  margin-left: 20%;
}

.content_active {
  background-color: white;
  /*display: inline-block;*/
  height: 100vh;
  text-align: right;
  color: black;
}

.content_active .mb-0 {
  color: rgb(0, 0, 0);
}
.content_active .mb-1 {
  color: rgb(172, 172, 172);
}

#top_profile_img {
  border-radius: 50%;
}

.content_active_element {
  text-align: center;
  /*margin: 0 auto;*/
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10%;
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
  /*padding-left: 0%;*/
  /*padding-right: 0%;*/
  /*width: 80%;*/
  height: 70vh;
}
.content_active_element_title {
  color: rgb(19, 89, 151);
  font-weight: bold;
  /* text-shadow: 1px 1px rgb(197, 197, 197);*/
}
