.content_formacion {
  display: inline;
  padding-top: 10%;
  padding-bottom: 20%;
  margin-top: 20%;
  margin-bottom: 20%;
  background-color: rgb(255, 255, 255);
  text-align: center;
}

.content_formacion .h5 {
  color: rgb(19, 89, 151);
  text-align: left;
  font-weight: bold;
  padding-left: 10%;
}

.formacion_li {
  display: table;
  border-spacing: 20px;
  border-collapse: separate;
  position: relative;
  list-style-type: none;
  margin-left: 10%;
  max-width: 80%;
  min-height: 10px;
  text-align: center;
}

.formacion_li_fila {
  display: table-row;
  width: auto;
  height: auto;
  box-sizing: border-box;
  text-align: center;
}

.formacion_card_element {
  display: table-cell;
  position: relative;
  background-color: rgb(255, 255, 255);
  border-radius: 4px;
  border-color: black; /*sacar*/
  border-style: thin; /*sacar*/
  padding: 20px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 30px;
  width: 15vw;
  height: 30vh;
  color: rgb(19, 89, 151);
  box-shadow: 0px 0px 10px rgb(163, 163, 163);
}

.formacion_card_contenido {
}

.formacion_card_periodo {
  text-align: left;
}

